import "./popup.css";

export const createPopup = () => {
  // Create the popup container
  const popupContainer = document.createElement("div");
  popupContainer.style.cssText = `
     width: 100%; height: 100vh; position: relative;
    background-color: red; display: flex;
    align-items: center; justify-content: center;
  `;

  // Create the popup content
  const popupContent = document.createElement("div");
  popupContent.style.cssText = `
    background-color: white; padding: 20px; border-radius: 5px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);
  `;

  // Create the heading
  const heading = document.createElement("h2");
  heading.textContent = "Kleb Jeb";
  popupContent.appendChild(heading);

  popupContainer.appendChild(popupContent);

  return popupContainer;
};
